import { h } from 'preact'
import { render, Component } from 'preact'
import fetch from 'unfetch'
import './style.css'

class App extends Component {
  constructor() {
    super()
    this.state = {
      data: null,
      /** @type {_nodetools.User} */
      user: null,
    }
  }
  render() {
    const { error, loading, user } = this.state
    if (loading) return (h('div',{},`Loading...`))
    if (error) return (h('div',{'className':"Error"},`Error: `,error))
    return (h('div',{},
      `Hello From Preact Component`
      ,user && ', ',
      user && h('span',{'className':"UserName"},user.name),
    ))
  }
  componentDidMount() {
    this.fetch()
  }
  async fetch() {
    this.setState({ loading: true })
    try {
      const res = await fetch('/api')
      const { error, user } =
        /** @type {!_nodetools.ServerResponse} */ (await res.json())
      if (error) return this.setState({ error })
      this.setState({ user })
    } catch(err) {
      let error = err
      if (err instanceof Event) {
        error = { message: 'Network error' }
      }
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
}

render( h(App), window['app'])

/**
 * @suppress {nonStandardJsDocs}
 * @typedef {import('../types').ServerResponse} _nodetools.ServerResponse
 */
/**
 * @suppress {nonStandardJsDocs}
 * @typedef {import('../types').User} _nodetools.User
 */